.navbar {
    color: #fff;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 4rem;

    position: sticky;
    top: 0;
    background-color: (--color-bg);
}

.navbar__scroll {
    color:#fff;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 4rem;

    position: sticky;
    top: 0;
    background-color: #162740;
}

.navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0rem 6rem;
}

.navbar-links_container {
    display: flex;
    flex-direction: row;
}

.navbar-links_container p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.navbar-menu svg {
    cursor: pointer;
    color:#fff
}

.navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

navbar-menu_container p {
    margin: 1rem 0;
}

@media screen and (max-width: 1050px) {
    .navbar-links_container {
        display: none;
    }

    .navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .navbar__bg {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .navbar__bg {
        padding: 2rem;
    }

    .navbar-menu_container {
        top: 20px;
    }

}

a:hover{
    color: rgba(0, 145, 255, 0.992)
}

.navbar__scroll a:hover{
    color:rgba(51, 204, 255, 0.992)
}