.projects {
    padding: 10rem 3rem;

    display: column;
    justify-content: center;
    text-align: center;
}

.title {
    font-size: 30px;
    color: rgba(0, 145, 255, 0.992);
}

.title h6 {
    color: rgb(249, 92, 92)
}

.job {
    display: flex;
    padding: 2rem;
}

.logo img{
    border-radius: 10px;
    float:left;
    width: 225px;
    margin-left:10px;
    
    
}

.text {
    display: column;
    text-align: left;
}


.text h2{
    font-size: 28px;
    color: rgba(255, 246, 166, 0.992);
}

.text h4{
    font-size: 24px;
}

.text p {
    font-size: 20px;
}

@media screen and (max-width: 1050px) {
    .projects {
        padding: 10rem 4rem;
    }

    .job {
        flex-direction: column;
    }

    .logo img {
        order: 1;
        display: flex;
        margin-left: none;
        float: none;
        text-align: center;
        
    }

    .text {
        order: 2;
        padding-left: none;
    }
}

@media screen and (max-width: 700px) {
    .projects {
        padding: 10rem 0rem;
    }

    .job {
        flex-direction: column;

    }

    .logo img {
        order: 1;
        display: flex;
        margin-left: none;
        float: none;
        text-align: center;

        
    }

    .text {
        order: 2;
        padding-left: none;
    }
}

@media screen and (max-width: 550px) {
    .projects {
        padding: 10rem 0rem;
    }

    .job {
        flex-direction: column;
    }
    
    .logo img {
        order: 1;
        display: flex;
        margin-left: none;
        float: none;
        text-align: center;

    }

    .text h2, h4, h6, p{
        order: 2;
        padding-left: none;
    }

}