.bg {
    padding: 10rem 2rem;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.profile img{
    border-radius: 10px;
    float:left;
    width: 325px;
    margin-left:10px;
}

.text {
    float: left;
    margin-left: 20px;
    font-size: 24px;
}

b{
    color: rgba(0, 145, 255, 0.992);
}

.text a {
    color: rgba(0, 145, 255, 0.992);
}

.text a:hover {
    color: rgba(246, 246, 41, 0.992);
}

@media screen and (max-width: 1050px) {
    .bg {
        padding: 10rem 4rem;
        flex-direction: column;
    }

    .profile {
        width: 325px;
        order: 1;
        display: flex;
        
    }

    .text {
        order: 2;
    }
}

@media screen and (max-width: 700px) {
    .bg {
        padding: 10rem 0rem;
        flex-direction: column;
    }

    .profile {
        width: 325px;
        order: 1;
        display: flex;
        margin-left: none;
        float: none;
    }

    .text {
        order: 2;
    }
}

@media screen and (max-width: 550px) {
    .bg {
        padding: 10rem 0rem;
        flex-direction: column;
    }

    .profile {
        width: 325px;
        order: 1;
        display: flex;
        margin-left: none;
        float: none;
    }

    .text {
        order: 2;
    }

}